import pako from 'pako';

// Uint8ArrayBase64
const uint8ArrayToBase64 = (uint8Array) => {
  return btoa(String.fromCharCode(...uint8Array));
};

// Base64Uint8Array
const base64ToUint8Array = (base64) => {
  return new Uint8Array(
    atob(base64)
      .split('')
      .map((char) => char.charCodeAt(0))
  );
};

export const compressData = (data) => {
  try {
    const jsonString = JSON.stringify(data);
    const compressed = pako.deflate(jsonString); // Uint8Array
    return uint8ArrayToBase64(compressed); // Base64
  } catch (error) {
    console.error(':', error);
    return null;
  }
};

export const decompressData = (compressedData) => {
  try {
    const decoded = base64ToUint8Array(compressedData); // Base64Uint8Array
    const decompressed = pako.inflate(decoded, { to: 'string' }); // 
    return JSON.parse(decompressed); // JSON
  } catch (error) {
    console.error(':', error);
    return null;
  }
};
